var account = null;
var contract = null;
var vaultcontract = null;

const NFTCONTRACT = "0x130CfcD3d22b4C5cf2Cd99f0d1BC536E8A0Da038";
const STAKINGCONTRACT = "0x8423474a3e59e9334e2D68b20FF95a21B8F0Fb1F"

const providerOptions = {
	binancechainwallet: {
		package: true
	  },
};

const web3Modal = new Web3Modal({
	network: "polygon",
	theme: "dark",
	cacheProvider: true,
	providerOptions 
  });

async function connectwallet() { 
      var provider = await web3Modal.connect();
      var web3 = new Web3(provider); 
      await provider.send('eth_requestAccounts'); 
      var accounts = await web3.eth.getAccounts(); 
      account = accounts[0]; 
      document.getElementById('wallet-address').textContent = account; 
      contract = new web3.eth.Contract(ABI, NFTCONTRACT);
	  vaultcontract = new web3.eth.Contract(POOLABI, STAKINGCONTRACT);
}

async function stakeit() {
	var tokenids = Number(document.querySelector("[name=stkid]").value);
	vaultcontract.methods.stake([tokenids]).send({from: account});
}

async function unstakeit() {
	var rawnfts = await vaultcontract.methods.tokensOfOwner(account).call();
    const arraynft = Array.from(rawnfts.map(Number));
    const tokenid = arraynft.filter(Number);
        tokenid.forEach(async (id) => {
          vaultcontract.methods.unstake([id]).send({from: account, gasLimit: 1000000});
        })
  }

async function claimit() {
	var tokenids = Number(document.querySelector("[name=stkid]").value);
	vaultcontract.methods.claim([tokenids]).send({from: account});
}

async function rewardinfo() {
   var tokenid = Number(document.querySelector("[name=stkid]").value);
   var rawearn = await vaultcontract.methods.earningInfo(account, ([tokenid])).call();
   var earned =  String(rawearn).split(",")[0];
   var earnedrwd = Web3.utils.fromWei(earned);
   var rewards = Number(earnedrwd).toFixed(2);
   document.getElementById('earned').textContent = rewards;
 }

async function enable() {
    contract.methods.setApprovalForAll(STAKINGCONTRACT, true).send({from: account});
}

class App extends Component {
	constructor() {
		super();
		this.state = {
			balance: [],
		};
	}
  
  render() {

  return (
<div className="App">
<div className='container'>
<div className='row' class="row justify-content-center">
<form class="border border-4 border-warning" style={{padding: "20px", width: "100%", borderRadius: "25px", backgroundColor: "#c62f42"}}>
    <h4 style={{color:"#FFFFFF"}}>STAKING POOL</h4>
    <Button variant="outline-warning" onClick={connectwallet} style={{marginBottom:"5px"}}>Connect Wallet</Button>
	<div></div>
	<Button variant="outline-warning" onClick={enable} style={{marginBottom:"5px"}}>Approve Staking</Button>
  <div id='wallet-address' class="text-uppercase fw-bold" style={{marginTop:"3px"}}>
    <label for="floatingInput">Please connect your wallet address</label>
  </div>
  <div style={{marginTop:"3px"}}>
    <label style={{width: "100%", color: "white"}} >Insert Azuki Skullhead #ID</label>
    <input style={{width: "100%", fontWeight: "bold",textAlign: "center", padding:"10px 18px", margin:"8px 0", boxSizing:"border-box"}} name="stkid"/>
  <div style={{marginTop:"5px"}} class="d-flex justify-content-around">
    <Button variant="outline-warning" onClick={stakeit}>STAKE</Button>
	  <Button variant="outline-warning" onClick={unstakeit}>UNSTAKE ALL</Button>
  </div>
  </div>
  </form>

  <div style={{height: "50px"}} ></div>
  <form class="border border-4 border-warning" style={{padding: "20px", width: "100%", borderRadius: "25px", backgroundColor: "#c62f42"}}>
  <h4 style={{color:"#FFFFFF"}}>STAKING REWARDS</h4>
	<Button variant="outline-warning" onClick={rewardinfo}>Calculate earned $SKULL</Button>
	<div id='earned' class="text-warning" style={{marginTop:"5px", fontSize:'25px',fontWeight:'bold',textShadow:"1px 1px 2px #000000"}}><p style={{fontSize:"20px"}}>Earned $SKULL</p></div>
	<div className='col-12 mt-2'>
      <div style={{color:'white'}}>Claim Rewards</div>
      <Button variant="outline-warning" className="mb-2" onClick={claimit}>Claim</Button>
  </div>
  </form>
  </div>
	</div>
 	</div>
  			);
	};
}

export default App;